.popup{
    position:fixed;
    top:0;
    left:0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.7);
    display:flex;
    justify-content: center;
    align-items: center;
}

.popup-inner{
    background-image: url('../../../public/images/label.png');
    background-size:     100% 100%;
    background-repeat:   no-repeat;
    background-position:  right 50% bottom 60%;; 
    color: black;

    position: relative;
    padding: 100%;
    padding:52px;
    /* max-width: 640px; */
    /* background-color: #777777; */
    border-radius: 10px;

}

.popup-inner .close-btn{
    position: absolute;
    top:16px;
    right: 16px;
    z-index: 9999; /* Set a high value for the popup content */
}

.close-btn {
    margin: 0px;
    position: relative;
    background-color: #4caf4f00;
    border: none;
    font-size: 15px;
    color: #FFFFFF;
    padding: 0px;
    width: 10px;
    text-align: center;
    transition-duration: 0.4s;
    text-decoration: none;
    overflow: hidden;
    cursor: pointer;
  }